import React from 'react'
import { ReactSelectListing } from '../ReactSelectListing.component';
import { Featured, statusG } from '../../../App/ReactSelect/SelectData';

export const SearchResult=()=>{

    return(
          <div className="grid_list_search_result">
        <div className="col-sm-12 col-md-4 col-lg-4 col-xl-5">
            <div className="left_area tac-xsd">
                <p>9 Search results</p>
            </div>
        </div>
        <div className="col-sm-12 col-md-8 col-lg-8 col-xl-7">
            <div className="right_area text-right tac-xsd grid-select-container">
                <ul className="grid-select-container1">
                    <li className="list-inline-item">
                    <span className="stts">Status:&nbsp; </span>
                    <ReactSelectListing data={statusG}/>
                    </li>
                    <li className="list-inline-item">
                    <span className="shrtby">Sort by:&nbsp;</span>
                    <ReactSelectListing data={Featured}/>
                    </li>
                </ul>
            </div>
        </div>
    </div>
    )
}